import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"
import Button from "react-bootstrap/Button"
import Container from "react-bootstrap/Container"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import Card from "react-bootstrap/Card"
import ListGroup from "react-bootstrap/ListGroup"
import Badge from "react-bootstrap/Badge"
import Stack from "react-bootstrap/Stack"
interface State {
  keys: string[]
}
/**
 * This is a React-based component template. The `render()` function is called
 * automatically when your component should be re-rendered.
 */
class TokenisationVisComponent extends StreamlitComponentBase<State> {
  public render = (): ReactNode => {
    // Arguments that are passed to the plugin in Python are accessible
    // via `this.props.args`. Here, we access the "name" arg.

    const data = this.props.args["data"]
    const isPOS = this.props.args["isToken"]
    const { theme } = this.props
    const style: React.CSSProperties = {}
    const items = Object.keys(data)

    return isPOS ? (
      <Card text="dark">
        <Card.Header>
          <h4>Tokenization</h4>
        </Card.Header>
        <ListGroup variant="flush">
          {items.map((item: string) => (
            <ListGroup.Item key={item}>
              <h5>{item} :</h5>
              {Object.keys(data[item]).map((sentenceID: string) => (
                <Row key={sentenceID}>
                  <Col md={1}>
                    <Button variant="dark" className="m-1" disabled>
                      {sentenceID}. <></>
                      <></>
                      <Badge>{data[item][sentenceID].length}</Badge>
                    </Button>
                  </Col>
                  <Col>
                    {data[item][sentenceID].map((token: string,index: number) => (
                      <Button variant="info" className="m-1" key={index}>
                        {token}
                      </Button>
                    ))}
                  </Col>
                </Row>
              ))}
            </ListGroup.Item>
          ))}
        </ListGroup>
      </Card>
    ) : (
      <Card text="dark">
        <Card.Header>
          <h4>Part-Of-Speech Tagging</h4>
        </Card.Header>
        <ListGroup variant="flush">
          {items.map((item: string) => (
            <ListGroup.Item key={item} >
              <h5>{item} :</h5>
              {Object.keys(data[item]).map((sentenceID: string) => (
                <Row key = {sentenceID}>
                  <Col md={1}>
                    <Button variant="dark" className="m-1" disabled>
                      {sentenceID}. <></>
                      <></>
                      <Badge>{data[item][sentenceID].length}</Badge>
                    </Button>
                  </Col>
                  <Col>
                  {data[item][sentenceID].map((tuple: [string, string],index:number) => (
                <Button variant="info" className="mr-1 mb-1" key={index}>
                  <Stack gap={1}>
                    <div>{tuple[0]}</div>
                    <Badge className="border" bg="danger">
                      {tuple[1]}
                    </Badge>
                  </Stack>
                </Button>
              ))}
                  </Col>
                </Row>
              ))}

            </ListGroup.Item>
          ))}
        </ListGroup>
      </Card>
    )
  }
}

// "withStreamlitConnection" is a wrapper function. It bootstraps the
// connection between your component and the Streamlit app, and handles
// passing arguments from Python -> Component.
//
// You don't need to edit withStreamlitConnection (but you're welcome to!).
export default withStreamlitConnection(TokenisationVisComponent)
